import React, { Component } from 'react';
import './index.css';

class EditItem extends Component {
    constructor(props) {
    super(props);
    this.state = { value: '', show: false };

    this.handleChange = this.handleChange.bind(this);    
    this.editItem = this.editItem.bind(this);
    this.showEditForm = this.showEditForm.bind(this);    
    };

    handleChange(event) {
        this.setState({value: event.target.value});
    };

    editItem(event) {
        const { value } = this.state;
        const { id, onClick } = this.props;        

        fetch('/api/v1/items/' + id, {
            method: 'PATCH',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({
                description: value,            
            })
        })
        .then(x => {onClick();});

        this.setState({ value: '', show: false });               
        event.preventDefault();
    };

    showEditForm() {
        const { show } = this.state;

        show ? this.setState({ show: false }) : this.setState({ show: true })
    }

    render() {
        const { show } = this.state;

        return (
            <div>
                {
                    show 
                    ?   <div onClick={this.showEditForm} className="fas fa-undo-alt showEdit"></div>            
                    :   <div onClick={this.showEditForm} className="fas fa-edit showEdit"></div>
                }

                { 
                    show 
                    ?   <div className="editContainer">
                            <div className="far fa-check-circle showEditOk" onClick={this.editItem}></div>
                            <input type="text" value={this.state.value} onChange={this.handleChange} placeholder="Edit item..." />                
                        </div>                        
                    :   null
                }

            </div>
        );
    };
};

export default EditItem;


