import React, { Component } from 'react';
import './index.css';

import AddItem from '../AddItem';
import DeleteItem from '../DeleteItem';
import EditItem from '../EditItem';

class Items extends Component {
    constructor(props) {
        super(props);
        this.state = { data: [], show: false };

        this.handleClick = this.handleClick.bind(this);
        this.getData = this.getData.bind(this);
    };

    getData() {
        const { id } = this.props;
        
        fetch('/api/v1/items/' + id)
        .then(response => {
            return response.json();
        }).then(data => {
            this.setState({ data: data.items });
        }).catch(function(err) {
            console.log(err)
        });
        
    };

    handleClick() {
        const { show } = this.state;
        if(show) { 
            this.setState({ show: false })
        } 
        else {
            this.getData();
            this.setState({ show: true })
        }
    };

    render() {
        const { data, show } = this.state;
        return (
            <div>

            {
                show 
                ? <div onClick={this.handleClick} className="far fa-eye-slash show"></div>            
                : <div onClick={this.handleClick} className="far fa-eye show"></div>
            }
            
            
            { 
                show
                ?   data.length !== 0 
                    ?   <div>
                            <AddItem id={this.props.id} onClick={this.getData} />
                            <div className="items" > 
                    
                                { data.map(item => {
                                return (
                                    <div className="item">
                                        <div className="itemName">{item.description}</div>                                    
                                        <EditItem id={item._id} onClick={this.getData} />
                                        <DeleteItem id={item._id} onClick={this.getData} />                                
                                    </div>
                                )
                            })}                    
                            </div>
                        </div>
                    :   <div>
                            <AddItem id={this.props.id} onClick={this.getData} />
                            <div className="item">
                                <div className="itemName">Empty</div>
                            </div>
                        </div>
                :   null
            }
            </div>
        )        
    };
};

export default Items;


