import React, { Component } from 'react';
import './index.css';

class AddList extends Component {
    constructor(props) {
    super(props);
    this.state = { value: '' };

    this.handleChange = this.handleChange.bind(this);
    this.addList = this.addList.bind(this);
    };

    handleChange(event) {
        this.setState({value: event.target.value});
    };

    addList(event) {
        const { value } = this.state;        

        fetch('/api/v1/lists/', {
            method: 'POST',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({
                description: value,            
            })
        })        
        .then(x => {
            this.props.onClick();
        });       

        this.setState({ value: '' });
        event.preventDefault();
    };

    render() {
        return (
            <div className="addContainer">
                <div className="add" onClick={this.addList}></div>
                <input type="text" value={this.state.value} onChange={this.handleChange} placeholder="New list..." />                
            </div>
            
        );
    };
};

export default AddList;


