import React, { Component } from 'react';
import './index.css';

class AddItem extends Component {
    constructor(props) {
    super(props);
    this.state = { value: '' };

    this.handleChange = this.handleChange.bind(this);
    this.addItem = this.addItem.bind(this);
    };

    handleChange(event) {
        this.setState({value: event.target.value});
    };

    addItem(event) {
        const { value } = this.state;
        const { id } = this.props;

        fetch('/api/v1/items/', {
            method: 'POST',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({
                description: value,
                listId: id            
            })
        })
        .then(x => {
            this.props.onClick();
        });       

        this.setState({ value: '' });
        event.preventDefault();
    };

    render() {
        return (
            <div className="addContainer">
                <div className="add" onClick={this.addItem} data-title="delete"></div>
                <input type="text" value={this.state.value} onChange={this.handleChange} placeholder="New item..." />                
            </div>
        );
    };
};

export default AddItem;


